        <div class="title" v-if="showTitle">小睡眠小测评</div>
        <div class="desc-block" :style="{backgroundColor: visiableFrame}">
          <div class="desc-block-title">{{ title }}</div>
          <div class="desc-block-desc" v-html="desc"></div> <!-- 夜色琉璃，浮生倥偬你凭栏垂眸，眼里荡漾开漫天灯火。你说星星拥抱月光，你说烟花亲吻沉云，你把深夜的钟声谱写成歌，温柔荡漾成眼底闪烁比秋天短，比世界长“为你，千千万万遍”晚安。 -->
          <div class="qrBlock">
