            <div class="qrBlock-img">
              <div class="img-outer">
                <img class="content-img" :src="qrUrl"/>
              </div>
              扫码马上测试
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 旧版本的广告 写死广告 -->
    <div class="share-template" v-if="!this.$root.amountData.advertisementId"> <!-- @click="this.$parent.actionWeixinShare" -->
      <a :href="this.$root.amountData.advertisementUrl">
        <img class="adPic" :src="this.$root.amountData.advertisementPic" />
      </a>
    </div>

    <!-- 顶部的广告 -->
    <template v-for="(li, index) in stickMaterial">
      <div @click="baiduEvent(li.data.materialData.id)" class="share-template animated fadeIn" :key="index" v-if="li.show"> <!-- @click="this.$parent.actionWeixinShare" -->
        <div @click="updateQueryStringParameter(li.data.materialData.data.link, 'article')">
          <img class="adPic" :src="li.data.materialData.data.picUrl" />
        </div>
      </div>
    </template>

    <div class="division" v-if="stickMaterial.length !== 0">- 更多精彩测评 -</div>

    <!-- 特定的素材 -->
    <div class="share-template animated fadeIn" v-for="li in material" :key="li.data.random"> <!-- @click="this.$parent.actionWeixinShare" -->
      <a @click="baiduEvent(li.data.materialData.id)" :href="li.data.materialData.data.link">
        <img class="adPic" :src="li.data.materialData.data.picUrl" />
      </a>
    </div>

    <!-- <div class="share-template" v-for="(li, index) in material" :key="index">
      <a :href="li.data.materialData.link">
        <img class="adPic" :src="li.data.materialData.pic" />
