<script>
import html2canvas from 'html2canvas'
import Axios from 'axios'
const QRCode = require('qrcode')
// 结果页面 本来想套一个出来的...  结果发现没有这个必要。
export default {
  name: 'imgContainer',
  data () {
    return {
      showTitle: true,
      background: require('../assets/cosleep_test_img_result-card@3x.png'),
      desc: '',
      qrUrl: '',
      visiableFrame: '',
      title: '',
      beBeingRandomMaterialId: new Set(), // 已经是随机素材的id就不需要添加进去了
      imgSrc: '', // 修复三星识别不出图片的问题
      materialsID: [], // 所有素材的Id
      stickMaterial: [], // 顶部的广告
      material: [] // 普通的
    }
  },
  methods: {
    updateQueryStringParameter (uri, key) {
      let value = this.$root.id
      if (!value) {
        return uri
      }
      let re = new RegExp('([?&])' + key + '=.*?(&|$)', 'i')
      let separator = uri.indexOf('?') !== -1 ? '&' : '?'
      if (uri.match(re)) {
        window.location.href = uri.replace(re, '$1' + key + '=' + value + '$2')
      } else {
        window.location.href = uri + separator + key + '=' + value
      }
    },
    countResult () {
      this.visiableFrame = this.$root.amountData.visiableFrame ? 'rgba(0, 0, 0, 0.5)' : '' // rgba(10, 86, 131, 0.5)
      // const score = parseFloat(sessionStorage.getItem('score'))
      const list = this.$root.amountData.itemList || []
      if (list.length > 0) {
        const randomNum = this.WJH.randomNum(0, list.length - 1)
        const i = list[randomNum]
        /* eslint-disable */
        i.background ? this.background = i.background : this.background = this.$root.style.resultBackground
        i.background ? this.showTitle = false : ''

        // this.desc = i.desc.replace(/\$\{score\}/g, score).replace(/\$\{name\}/g, localStorage.getItem('name') || '你') // 还有一个ba
        this.desc = i.desc.replace(/\$\{name\}/g, localStorage.getItem('name') || '你').replace('script', '')

        // this.title = (i.title || '').replace(/\$\{score\}/g, score).replace(/\$\{name\}/g, localStorage.getItem('name') || '你')
        this.title = (i.title || '').replace(/\$\{name\}/g, localStorage.getItem('name') || '你')

        if (i['shareTitle'] && i['shareDesc']) {
          // 当前Url
          const test = `{"random":0.02145202202627705,"desc":"sadsad","background":"","title":"sad","shareTitle":"sad","shareDesc":"sad"}`
          const localUrl = window.location.href
          // const url = localUrl.split('#')[0].includes('?')
          //   ? localUrl.replace('?', `?rd=${JSON.stringify(i)}&`)
          //   : localUrl.split('#')[0] + `?rd=${JSON.stringify(i)}/#/` + localUrl.split('#')[1]
          const url = localUrl.split('#')[0].includes('?')
            ? localUrl.replace('?', `?rd=${test}&`)
            : localUrl.split('#')[0] + `?rd=${test}/#/` + localUrl.split('#')[1]
          console.log(url)
          this.$root.shareM(i.shareTitle, i.shareDesc, false, url)
        }
      }

      Axios.post(this.$root.url + '/evaluate-miniapp/question/increment/solve-count', { id: this.$root.id }).then(() => {})
      // console.log(JSON.parse(list))
      // const resultGroup = []
      // for (let i of JSON.parse(list)) {
      //   if (score >= i.min && score <= i.max) {
      //     resultGroup.push(i)
      //     console.log(i.background)
      //   }
      // }

      // if (resultGroup.length >= 1) { // 随机结果
      //   const randomNum = this.WJH.randomNum(0, resultGroup.length - 1)
      //   const i = resultGroup[randomNum]
      //   /* eslint-disable */
      //   i.background ? this.background = i.background : this.background = this.$root.style.resultBackground
      //   i.background ? this.showTitle = false : ''
      //   this.desc = i.desc.replace(/\$\{score\}/g, score).replace(/\$\{name\}/g, localStorage.getItem('name') || '你') // 还有一个backgoround 没有设置好
      //   this.title = (i.title || '').replace(/\$\{score\}/g, score).replace(/\$\{name\}/g, localStorage.getItem('name') || '你')
      //   console.log(i.hasOwnProperty('shareTitle') && i.hasOwnProperty('shareDesc'), '')
      //   if (i.hasOwnProperty('shareTitle') && i.hasOwnProperty('shareDesc')) {
      //     this.$root.shareM(i.shareTitle, i.shareDesc)
      //   }
      //   Axios.post(this.$root.url + '/evaluate-miniapp/question/increment/solve-count', { id: this.$root.id }).then(() => {})
      //   return
      // }
    },
    parseToPic () {
      const dom = document.querySelector('#canvas')
      // const container = document.querySelector('.outer-img')
      html2canvas(dom, {
        useCORS: true,
        onclone: (document) => {
          document.querySelector('#canvas').style.visibility = 'initial'
        }
      }).then((canvas) => {
        const img = new Image()
        img.src = canvas.toDataURL()
        img.style.width = '100%'

        img.style.position = 'absolute'
        img.style.left = '50%'
        img.style.top = '50%'
        img.style.transform = 'translateX(-50%) translateY(-50%)'

        this.imgSrc = img.src
        // container.appendChild(img)
        // dom.innerHTML = ''
        // dom.appendChild(img)
        // dom.style.visibility = 'initial'
        // dom.classList.add('change-canvas-border-radius')
      })
    },
    init () {
      // this.$parent.bg_c = '#fff'
      // alert((window.location.href.split('#')[0] + '&sharepic').replace(/&code.*?(?=\&)/, '').replace(/&state.*?(?=\&)/, ''))
      QRCode.toDataURL((window.location.href.split('#')[0] + '&sharepic').replace(/&code.*?(?=\&)/, '').replace(/&state.*?(?=\&)/, ''))
        .then(url => {
          this.qrUrl = url
          setTimeout(() => {
            this.parseToPic()
          }, 200)
        })
        .catch(err => {
          console.error(err, 'asderr')
        })
    },
    // 获取random === true 的id
    async getMaterialsId () {
      return new Promise((resolve) => {
        this.$axios.get('/evaluate-miniapp/material/all/randomTrue/id').then(res => {
          this.materialsID = res.data.data
          resolve('done')
        })
      })
    },
    getAdData (id) {
      this.$axios.get('/evaluate-miniapp/ad/module/read', { params: { id } }).then(async (res) => {
        await this.getMaterialsId()
        const data = res.data.data[0]
        for (let i of data) {
          // 检测APP显示还是微信显示 目前只有置顶有platForm 所以 没有则显示
          i.show = false
          console.log(
            this.$root.platForm,
            i.data.platForm
          )
          if (!i.data.platForm && i.data.platForm !== 0) {
            i.show = true
          }
          if (this.$root.platForm === +i.data.platForm) {
            i.show = true
          }
          if (i.data.platForm === 2) {
            i.show = true
          }
          if (i.typeName !== 'randomMaterial') {
            i.data.materialData = await this.getSingleMaterial(i.data.id)
          } else {
            // 过滤已经展示了的随机素材
            let id = ''
            this.beBeingRandomMaterialId.add(id)
            let maxCount = 0 // 最大检测 避免没有随机库了
            while (id = this.materialsID[this.WJH.randomNum(0, this.materialsID.length - 1)]) {
              if (!this.beBeingRandomMaterialId.has(id)) {
                maxCount += 1
                const data = await this.getSingleMaterial(id)
                // 去除自己的随机
                if (maxCount >= 20 || window.location.href.split('#')[0].includes(data.data.link.split('#')[0])) {
                } else {
                  i.data.materialData = data
                  break
                }
              }
            }
          }
        }
        let lastStickMaterialIndex = 0
        for (let i = 0;i < data.length;i++) {
          if (data[i].typeName === 'stickMaterial') {
            lastStickMaterialIndex = i
          }
        }
        this.stickMaterial = data.splice(0, lastStickMaterialIndex + 1)
        this.material = data
      })
    },
    // click 事件 百度统计提交id
    baiduEvent (id) {
      _hmt.push(['_trackEvent', 'nav', 'click', `素材`, id])
    },
    // 获取单个素材
    async getSingleMaterial (id) {
      const data = await new Promise(async (resolve) => {
        await this.$axios.get('/evaluate-miniapp/material/read', { params: { id } }).then(res => {
          resolve(res.data.data)
        })
      })
      return data
    }
  },
  mounted () {
    console.log('a')
    setTimeout(() => {
      if (!this.$root.amountData) {
        this.$router.push('./')
        return
      }

      this.init()
      this.desc = this.$root.amountData.defaultDesc // 默认desc文案
      this.countResult()
      this.getAdData(this.$root.amountData.advertisementId)
    }, 0)
  }
}
</script>

